	font-weight: normal;
	font-style: normal;
}

body {
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: #646464;
	font-weight: normal;
}

.btn {
	display: inline-block;
	cursor: pointer;
	text-align: center;
	vertical-align: top;
	text-decoration: none;
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: white;
	font-weight: normal;
	background: linear-gradient(#feab16, #ff7500) #ff9000;
	padding: 10px 30px;
	border-radius: 4px;
	border: 2px solid #ff9000;
	transition: all 0.3s ease;
	box-shadow: 0px 0px 3px 1px #D2D2D2;
}

.btn:hover {
	background: #ff9451;
}

.btn:active {
	background: #f5843e;
}

.btn--call {
	float: right;
	max-width: 210px;
}

.btn--features {
	min-width: 155px;
}

.btn--houses {
	min-width: 155px;
	margin-left: 60px;
}

.btn--form-submit {
	width: 390px;
	box-sizing: border-box;
	height: 55px;
	font-size: 20px;
	text-shadow: -1px 1px 1px #444;
}

.btn--footer {
	width: 100%;
	display: block;
	text-shadow: -1px 1px 1px #444;
	font-size: 20px;
	height: 55px;
	box-shadow: 0 0 3px 1px #787575;
}

.btn--popup {
	width: 280px;
	box-sizing: border-box;
	display: inline-block;
	transition: none;
}

.btn-link {
	font-size: 15px;
	color: #029dff;
	display: block;
	margin-top: 4px;
	font-size: 17px;
}

.btn-link--footer {
	font-size: 18px;
	display: inline-block;
}

input[type="text"],
textarea,
select,
input[type="password"] {
	border: none;
	outline: none;
	background: white;
	box-shadow: 1px -1px 1px 0px #eceaea;
	border: 1px solid #f1f2f6;
	box-sizing: border-box;
	width: 100%;
	height: 55px;
	color: #646464;
	border-radius: 4px;
	padding: 5px 30px;
}

textarea:hover,
textarea:focus,
input[type="text"]:hover,
input[type="text"]:focus {
	border: 1px solid #b7b7b7;
}

label {
	cursor: pointer;
}

button {
	outline: none;
	cursor: pointer;
}

input::-webkit-input-placeholder {
	opacity: 0.8;
}

input::-moz-placeholde {
	opacity: 0.8;
}

select {
	appearance: none;
	background: white url("../img/arrow-select.png") no-repeat right;
	cursor: pointer;
	padding-right: 15px;
	padding-left: 15px;
}

textarea {
	width: 100%;
	height: 205px;
	resize: none;
	padding-top: 15px;
}

input[type="hidden"] {
	width: 1px;
	height: 1px;
	opacity: 0;
}

