	z-index: 9980;
	right: 0; /* ╨б╨┐╤А╨░╨▓╨░ 0 */
	top: 0; /* ╨б╨▓╨╡╤А╤Е╤Г 0 */
	left: 0; /* ╨б╨╗╨╡╨▓╨░ 0 */
	bottom: 0; /* ╨б╨╜╨╕╨╖╤Г 0 */
	visibility: hidden; /* ╨Ш╨╖╨╜╨░╤З╨░╨╗╤М╨╜╨╛ ╨╜╨╡╨▓╨╕╨┤╨╕╨╝╤Л╨╣ */ /* transition ╤Б╤В╨░╤А╤В */
	transition: 0.5s ease;
}

.overlay:target {
	visibility: visible; /* ╨Ф╨╡╨╗╨░╨╡╤В ╨▓╨╕╨┤╨╕╨╝╤Л╨╝ */
	opacity: 0.8; /* ╨д╨╛╨╜ ╤Б╤В╨░╨╜╨╛╨▓╨╕╤В╤Б╤П ╨┐╨╛╨╗╨╜╨╛╤Б╤В╤М╤О ╨┐╤А╨╛╨╖╤А╨░╤З╨╜╤Л╨╝ */
}

.popup {
	position: fixed; /* ╨д╨╕╨║╤Б╨╕╤А╨╛╨▓╨░╨╜╨╜╨╛╨╡ ╤А╨░╤Б╨┐╨╛╨╗╨╛╨╢╨╡╨╜╨╕╨╡ */
	visibility: hidden; /* ╨Ш╨╖╨╜╨░╤З╨░╨╗╤М╨╜╨╛ - ╤Б╨┐╤А╤П╤В╨░╨╜ */
	z-index: 9999; /* z-index */
	transform: translateX(-50%);
	left: 50%;
	top: 20px;
	width: 370px;
	padding: 25px 45px;
	background-color: #f4f4f4; /* ╨д╨╛╨╜ */
	border-radius: 7px;
}

.overlay:target + .popup {
	opacity: 1; /* ╨г╨▒╨╕╤А╨░╨╡╤В ╨┐╤А╨╛╨╖╤А╨░╤З╨╜╨╛╤Б╤В╤М */
