}

.popup-close {
	display: none;
	width: 3px;
	height: 20px;
	background: red;
	position: absolute;
	top: 7px;
	right: 15px;
	font-size: 0px;
	transform: rotate(45deg);
}

.popup-close:after {
	content: "";
	display: block;
	transform: rotate(-90deg);
	width: 3px;
	height: 20px;
	background: red;
	position: absolute;
	top: 0;
}

.popup__logo {
	margin: 10px auto 30px;
}

.popup__logo img {
	display: block;
	margin: 0 auto;
}

.popup__title {
	font-size: 25px;
	line-height: 31px;
	color: #6a6a6a;
	text-align: center;
	margin-bottom: 20px;
}

.popup__description {
	margin-bottom: 30px;
}

.popup__form-line {
	margin-bottom: 15px;
}

.popup__btn-line {
	text-align: center;
	margin: 20px 0;
}

.popup__footer {
	color: #515151;
	letter-spacing: 1px;
	font-size: 17px;
	text-align: center;
	padding: 0 40px;
}

