}

[type=range]::-webkit-slider-runnable-track {
	width: 100%;
	height: 13px;
	cursor: pointer;
	transition: all .2s ease;
	box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
	background: linear-gradient(#feab16, #ff7500);
	border: 2px solid #ff9000;
	border-radius: 7px;
}

[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
	border: 0px solid #fff;
	height: 30px;
	width: 25px;
	border-radius: 8px;
	background: url("../img/inputpoint.png") no-repeat;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -10.5px;
}

[type=range]::-moz-range-track {
	width: 100%;
	height: 13px;
	cursor: pointer;
	transition: all .2s ease;
	box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
	background: linear-gradient(#feab16, #ff7500);
	border: 2px solid #ff9000;
	border-radius: 7px;
}

[type=range]::-moz-range-thumb {
	box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
	border: 0px solid #fff;
	height: 30px;
	width: 25px;
	border-radius: 8px;
	background: url("../img/inputpoint.png") no-repeat;
	cursor: pointer;
}

[type=range]::-ms-track {
	width: 100%;
	height: 13px;
	cursor: pointer;
	transition: all .2s ease;
	background: transparent;
	border-color: transparent;
	border-width: 25px 0;
	color: transparent;
}

[type=range]::-ms-fill-lower {
	box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
	background: linear-gradient(#feab16, #ff7500);
	border: 2px solid #ff9000;
	border-radius: 14px;
}

[type=range]::-ms-fill-upper {
	box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
	background: linear-gradient(#feab16, #ff7500);
	border: 2px solid #ff9000;
