	background-color: black; /* ╨ж╨▓╨╡╤В ╤Д╨╛╨╜╨░ */
	opacity: 0; /* ╨Ш╨╖╨╜╨░╤З╨░╨╗╤М╨╜╨╛ ╨╜╨╡╨┐╤А╨╛╨╖╤А╨░╤З╨╜╤Л╨╣ */
	position: fixed; /* ╨д╨╕╨║╤Б╨╕╤А╨╛╨▓╨░╨╜╨╜╨╛╨╡ ╨┐╨╛╨╖╨╕╤Ж╨╕╨╛╨╜╨╕╤А╨╛╨▓╨░╨╜╨╕╨╡ */
	z-index: 9980;
	right: 0; /* ╨б╨┐╤А╨░╨▓╨░ 0 */
	top: 0; /* ╨б╨▓╨╡╤А╤Е╤Г 0 */
	left: 0; /* ╨б╨╗╨╡╨▓╨░ 0 */
	bottom: 0; /* ╨б╨╜╨╕╨╖╤Г 0 */
	visibility: hidden; /* ╨Ш╨╖╨╜╨░╤З╨░╨╗╤М╨╜╨╛ ╨╜╨╡╨▓╨╕╨┤╨╕╨╝╤Л╨╣ */ /* transition ╤Б╤В╨░╤А╤В */
	transition: 0.5s ease;
}

.overlay:target {
	visibility: visible; /* ╨Ф╨╡╨╗╨░╨╡╤В ╨▓╨╕╨┤╨╕╨╝╤Л╨╝ */
	opacity: 0.8; /* ╨д╨╛╨╜ ╤Б╤В╨░╨╜╨╛╨▓╨╕╤В╤Б╤П ╨┐╨╛╨╗╨╜╨╛╤Б╤В╤М╤О ╨┐╤А╨╛╨╖╤А╨░╤З╨╜╤Л╨╝ */
}

.qualification-slider__big-images {
	position: fixed; /* ╨д╨╕╨║╤Б╨╕╤А╨╛╨▓╨░╨╜╨╜╨╛╨╡ ╤А╨░╤Б╨┐╨╛╨╗╨╛╨╢╨╡╨╜╨╕╨╡ */
	visibility: hidden; /* ╨Ш╨╖╨╜╨░╤З╨░╨╗╤М╨╜╨╛ - ╤Б╨┐╤А╤П╤В╨░╨╜ */
	z-index: 9999; /* z-index */
	transform: translateX(-50%);
	left: 45%;
	top: 0px;
}

.qualification-slider__big-images img {
	display: block;
	height: 600px;
	width: 576px;
}

.overlay:target + .qualification-slider__big-images {
	opacity: 1; /* ╨г╨▒╨╕╤А╨░╨╡╤В ╨┐╤А╨╛╨╖╤А╨░╤З╨╜╨╛╤Б╤В╤М */
	visibility: visible; /* ╨Ф╨╡╨╗╨░╨╡╤В ╨▓╨╕╨┤╨╕╨╝╤Л╨╝ */
}

.questions {
	font-size: 0;
	padding: 70px 0 60px;
}

.questions__block {
	display: inline-block;
	vertical-align: middle;
}

.questions__block:first-child {
	margin-right: 130px;
}

.questions__title {
	font-family: "Panton", Arial, sans-serif;
	font-size: 40px;
	line-height: 47px;
	color: #ff9000;
}

.questions__description {
	color: #646464;
	font-size: 20px;
	line-height: 30px;
	margin-top: 10px;
}

.questions__link {
	text-decoration: none;
	font-family: "Panton", Arial, sans-serif;
	font-size: 50px;
	line-height: 50px;
	color: #ff9000;
}

.reviews {
	background: #f9faff;
	padding: 85px 0 0;
}

.reviews__title {
	font-family: "Panton", Arial, sans-serif;
	font-size: 50px;
	line-height: 50px;
	color: #ff9000;
	margin: 0;
	font-weight: normal;
	margin: 0;
	padding-left: 60px;
}

.reviews__description {
	color: #646464;
	font-size: 20px;
	line-height: 30px;
	margin: 0;
	margin-top: 25px;
	padding-left: 60px;
}

.reviews-slider {
	position: relative;
	padding: 40px 0 40px;
}

.reviews-slider__controls {
	position: absolute;
	bottom: 35px;
	transform: translateX(-50%);
	left: 50%;
	font-size: 0;
	display: none;
}

.reviews-slider__control {
	width: 66px;
	height: 7px;
	background: #e8e8e8;
	margin-right: 10px;
	display: inline-block;
	vertical-align: top;
	border-radius: 4px;
}

.reviews-slider__control:last-child {
	margin-right: 0;
}

.reviews-slider__control--active {
	background: linear-gradient(#feab16, #ff7500) #ff9000;
}

.reviews-slider__arrow {
	font-size: 0px;
	text-decoration: none;
}

.reviews-slide {
	width: 1030px;
	box-sizing: border-box;
	border: 1px solid #e7e9ed;
	background: white;
	border-radius: 10px;
	margin: 0 auto;
	padding: 20px;
	margin-bottom: 20px;
}

.reviews-slide:after {
	content: "";
	display: table;
	clear: both;
}

.reviews-slide__image {
	float: left;
	padding: 10px;
	border: 1px solid #c9cac9;
	border-radius: 10px;
	box-sizing: content-box;
	width: 180px;
}

.reviews-slide__image img {
	display: block;
}

.reviews-slide__description {
	float: right;
	width: 760px;
}

.reviews-slide__name {
	font-size: 25px;
	line-height: 31px;
}

.reviews-slide__title {
	font-size: 20px;
	line-height: 26px;
	margin: 10px 0 10px;
	color: #ff9000;
}

.reviews-slide__review {
	font-size: 15px;
	line-height: 21px;
}

